import { BANNER_STATES } from '../banner/banner-utils';
export const INLINE_NOTIFICATION_STATES = ['success', ...BANNER_STATES];
export const getInlineNotificationIconName = (state) => {
    const stateToIconMap = {
        neutral: 'information-filled', // deprecated
        info: 'information-filled',
        warning: 'warning-filled',
        success: 'success-filled',
        error: 'error-filled',
    };
    return stateToIconMap[state];
};
export const getContentAriaAttributes = (state, labelId, descriptionId) => {
    const isAlert = state === 'warning' || state === 'error';
    return {
        role: isAlert ? 'alert' : 'status',
        'aria-live': isAlert ? 'assertive' : 'polite',
        'aria-labelledby': labelId,
        'aria-describedby': descriptionId,
    };
};
